<template>
  <div>
    <b-row cols="1" cols-lg="2" class="no-print">
      <b-col class="p-1">
        <b-form-group
          id="input-group-acc-id"
          label="ID"
          label-for="input-acc-id"
          class="text-left font-weight-bold"
        >
          <b-form-input
            id="input-acc-id"
            v-model="accId"
            placeholder="00 1E C0 1A 2B 3C"
          ></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row cols="1" cols-lg="2" class="no-print">
      <b-col class="p-1">
        <b-button variant="outline-primary" v-on:click="clearInputs"
          >Reset</b-button
        >
      </b-col>
    </b-row>

    <b-row cols="1" class="mt-5">
      <b-col class="p-1">
        <div v-if="isAccIdInputValid">
          <vue-qr v-bind:text="accIdFormatted" />
          <div id="qr-label-text" class="text-mono">{{ accIdFormatted }}</div>
        </div>

        <div
          v-if="accId && !isAccIdInputValid"
          class="alert alert-danger"
          role="alert"
        >
          ID is not valid. It must be 12 hexadecimal (0-9, A-F) characters. For
          example, "00 1E C0 1A 2B 3C"
        </div>

        <div v-if="!accId" class="alert alert-warning" role="alert">
          Enter an ACC ID
        </div>
      </b-col>
    </b-row>
  </div>
</template>

<script>
const ACC_ID_PARAM_KEY = "id"
const DEFAULT_ACC_ID = ""

export default {
  name: "AccQrCode",
  data() {
    return {
      accId: DEFAULT_ACC_ID,
    }
  },
  computed: {
    accIdNormalized: function () {
      return this.accId.replace(/(\s+)|(:)|(-)/g, "").toUpperCase()
    },
    isAccIdInputValid: function () {
      return this.accIdNormalized.match(/^[0-9A-Fa-f]{12}$/) !== null
    },
    accIdFormatted: function () {
      const oui1 = this.accIdNormalized.slice(0, 2)
      const oui2 = this.accIdNormalized.slice(2, 4)
      const oui3 = this.accIdNormalized.slice(4, 6)
      const nic1 = this.accIdNormalized.slice(6, 8)
      const nic2 = this.accIdNormalized.slice(8, 10)
      const nic3 = this.accIdNormalized.slice(10, 12)
      return `ID: ${oui1} ${oui2} ${oui3} ${nic1} ${nic2} ${nic3}`
    },
  },
  watch: {
    accId: function (newValue) {
      let newQuery = {}
      if (newValue) {
        newQuery = { id: newValue }
      }
      this.$router.replace({
        query: newQuery,
      })
    },
  },
  methods: {
    clearInputs: function () {
      this.accId = DEFAULT_ACC_ID
    },
  },
  created() {
    const queryAccId = this.$route.query[ACC_ID_PARAM_KEY]
    if (queryAccId) {
      this.accId = queryAccId
    }
  },
}
</script>

<style scoped>
#qr-label-text {
  font-weight: 500;
  font-size: 1.2rem;
  font-style: monospace;
}
</style>
